import React ,{Component} from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {ProductConsumer} from  '../context/Context';
import PropTypes from 'prop-types'

class Product extends Component{
  render(){
      const {id, title, img, price, inCart} = this.props.product;
    return(
      //my-3 means margin top-bottom and md means medium screen...
        <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3"> 
              <div className="card">
                  <ProductConsumer>
                    {/*Havin access to the value and the method*/}
                    {value => (
                      <div className="img-container p-5 align-self-center "   onClick={ () => value.handleDetail(id) }>
                                <Link to="/details">
                                    <img src={img} alt="product" className="card-img-top" style={{height:'6.5rem', width:'7rem'}} />
                                </Link>
                                
                            
                                <button className="cart-btn " disabled={inCart ? true : false }
                                 onClick={() =>  {
                                     value.addToCart(id)
                                      value.openModal(id)
                                      }}>
                                   
                                
                            {inCart ? (<p className="text-capitalize mb-0" disabled> {" "}in Cart</p> ): ( <i className="fas fa-cart-plus "/>)}
                  </button>
                </div>
                    )}
                    
                </ProductConsumer>
                  {/*Card Footer     d-flex >> displayFlex*/}
                    <div className="card-footer d-flex justify-content-between"> 
                              <p className="align-self-center  mb-0">
                                  {title}
                              </p>
                              <h5 className="font-italic mb-0">
                                      <span className="mr-1">$</span>
                                        {price}
                              </h5>
                    </div>
              </div>

        </ProductWrapper>
    );
  }
}

export default Product;


//proptype is used to parse in values  
  Product.propType = {
    product:PropTypes.shape({
      id:PropTypes.number,
      img:PropTypes.string,
      title:PropTypes.string,
      price:PropTypes.number,
      inCart: PropTypes.bool
    }).isRequired
  } 


const ProductWrapper = styled.div`
  .card{   
    border-color:transparent;
    border-radius:10px;
    transition: all 1s linear;
  }

  .card-footer{
    background:transparent;
    border-top:transparent;
  }
  :&hover{
      .card{
        border:0.04rem solid rgba(0, 0, 0, 0.2);
        box-shadow:2px 2px 5px 0px rgba(0, 0, 0, 0.2);
      }

      .card-footer{
        background: rgba(247, 247, 247);
      }
  }

  .img-container{
    position:relative;
    overflow:hidden;

  }

  .card-img-top{
    transition: all 1s Linear;
  }

  .img-container:hover .card-img-top{
        transform:scale(1.2);
  }
  .cart-btn{
    position:absolute;
    bottom:0;
    right:0;
    padding:0.2rem 0.4rem;
    background: #000;
    color:#fff;
    border:none;
    font-size:1.4rem;
    border-radius:0.5rem 0 0.5rem 0;
    transition: all 1s Linear;

  }


`
;